import React, { useEffect } from "react"
import Grid from "@material-ui/core/Grid"

import GpsCalibrationCard from "../components/GpsCalibrationCard"
import Networking from "../networking"

interface Props {
  networking: Networking
  setTopBarTitle: React.Dispatch<React.SetStateAction<string>>
}

export default function Page({ networking, setTopBarTitle }: Props) {
  useEffect(() => {
    setTopBarTitle("Configuration")
  }, [])
  return (
    <Grid container spacing={2} alignItems="flex-start">
      <Grid item>
        <GpsCalibrationCard networking={networking} />
      </Grid>
    </Grid>
  )
}
