import React, { useState } from "react"

import { makeStyles } from "@material-ui/core/styles"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import Typography from "@material-ui/core/Typography"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  body1: {
    margin: theme.spacing(2.5),
  },
  textField: {
    width: "15.5rem",
    margin: theme.spacing(1),
  },
  button: {
    margin: theme.spacing(1),
  },
}))

const powerListItems = [
  "Power on the control station radio",
  "Ensure the 48V battery is fully charged and plug it into the controller box",
  "Power on the controller 48V and 12V",
  "Ensure the vehicle Ethernet radio is connected to PoE",
  "Ensure GPS has 12V supply",
]

export default function Component() {
  const classes = useStyles()

  const powerList = powerListItems.map((item, index) => (
    <Typography variant="body1" align="justify" className={classes.body1} key={index}>
      {`${index + 1}. ${item}`}
    </Typography>
  ))

  return (
    <Card variant="outlined">
      <CardContent>
        <Typography variant="h5" className={classes.title}>
          Instructions
        </Typography>

        {powerList}
      </CardContent>
    </Card>
  )
}
