import React, { useState } from "react"

import { makeStyles } from "@material-ui/core/styles"
import clsx from "clsx"
import Card from "@material-ui/core/Card"
import CardContent from "@material-ui/core/CardContent"
import CardActions from "@material-ui/core/CardActions"
import Collapse from "@material-ui/core/Collapse"
import IconButton from "@material-ui/core/IconButton"
import Typography from "@material-ui/core/Typography"
import { RobotState } from "../shared/interfaces"
import ExpandMoreIcon from "@material-ui/icons/ExpandMore"
import { GNSS_FIX_TYPE } from "../shared/constants"

const useStyles = makeStyles((theme) => ({
  title: {
    margin: theme.spacing(1),
  },
  body: {
    margin: theme.spacing(1),
  },
  expand: {
    margin: "0 auto",
    paddingTop: 0,
    paddingBottom: 0,
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  cardContent: {
    paddingBottom: 0,
  },
}))

export default function Component({ robotState }: { robotState: RobotState }) {
  const classes = useStyles()
  const [expanded, setExpanded] = React.useState(false)

  function handleExpandClick() {
    setExpanded(!expanded)
  }

  return (
    <Card variant="outlined">
      <CardContent className={classes.cardContent}>
        <Typography variant="h5" className={classes.title}>
          GPS
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Status: {GNSS_FIX_TYPE[robotState.gps.status]}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Latitdue (deg): {robotState.gps.latitude.toFixed(9)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Longitude (deg): {robotState.gps.longitude.toFixed(9)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Heading (deg): {((robotState.gps.heading / Math.PI) * 180).toFixed(2)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          North Velocity (m/s): {robotState.gps.northVelocity.toFixed(2)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          East Velocity (m/s): {robotState.gps.eastVelocity.toFixed(2)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Linear Speed (m/s):{" "}
          {Math.sqrt(
            Math.pow(robotState.gps.eastVelocity, 2) + Math.pow(robotState.gps.northVelocity, 2)
          ).toFixed(2)}
        </Typography>

        <Typography variant="body1" className={classes.body}>
          Angular Velocity (deg/s): {((robotState.gps.zAngularVelocity / Math.PI) * 180).toFixed(0)}
        </Typography>

        {/* Hidden Region */}
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          {/* <Typography variant="body1" className={classes.body}>
            X Magnetometer (mG): {robotState.gps.xMagnetometer.toFixed(4)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Y Magnetometer (mG): {robotState.gps.yMagnetometer.toFixed(4)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Z Magnetometer (mG): {robotState.gps.zMagnetometer.toFixed(4)}
          </Typography> */}

          <Typography variant="body1" className={classes.body}>
            System Status: {robotState.gps.systemStatus.toString(2)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Filter Status: {robotState.gps.filterStatus.toString(2)}
          </Typography>

          {/* <Typography variant="body1" className={classes.body}>
            X Acceleration (m/s2): {robotState.gps.xAcceleration.toFixed(2)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Y Acceleration (m/s2): {robotState.gps.yAcceleration.toFixed(2)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Z Acceleration (m/s2): {robotState.gps.zAcceleration.toFixed(2)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Latitude Varance: {robotState.gps.latitudeVariance.toFixed(2)}
          </Typography>

          <Typography variant="body1" className={classes.body}>
            Longitude Varance: {robotState.gps.longitudeVariance.toFixed(2)}
          </Typography> */}
        </Collapse>
      </CardContent>

      <CardActions disableSpacing>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
    </Card>
  )
}
